h1 {
  @apply text-4xl;
}

h2 {
  @apply text-3xl;
}

h3 {
  @apply text-2xl;
}

h4 {
  @apply text-xl;
}

h5 {
  @apply text-lg;
}

h6 {
  @apply text-base;
}

body {
  @apply font-sans;
}

p {
  &:not(:first-child) {
    @apply mt-4;
  }
  &.js-flag-message {
    @apply mt-0;
  }
}
dt {
  @apply font-bold leading-normal;
}
